@font-face {
  font-family: "NSacEB";
  src: url("/public/NanumSquare_acEB.ttf") format("truetype");
  font-weight: normal;
}
  
* {
  font-family: 'NSacEB', sans-serif;
}

.textarea-container {
  width: 80%;
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  position: relative;
  z-index: 1;
}
  
.textarea {
  width: 80%;
  min-height: 250px;
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #232629;
  font-size: 16px;
  line-height: 1.5;
  resize: none;
  box-sizing: border-box;
  color: white;
}
  
textarea::-webkit-scrollbar {
  width: 5px;
}
  
textarea::-webkit-scrollbar-track {
  background: none;
}
  
textarea::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 6px;
  margin-left: 50px;
}

textarea:focus {
  outline: none;
}

.length-counter {
  position: absolute;
  bottom: 20px;
  right: 90px;
  font-size: 12px;
  color: #999;
}

@media (max-width: 768px) {
  .length-counter {
    right: 60px;
  }
}