* {
    font-family: sans-serif;
}

.select-menu {
  position: relative;
  display: inline-block;
}
  
.select-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px;
  background-color: #232629;
  color: #fff;
  cursor: pointer;
}
  
.arrow {
  margin-left: 8px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 6px 6px 0 6px;
  border-color: #fff transparent transparent transparent;
  transition: transform 0.2s;
}
  
.arrow.up {
  transform: rotate(180deg);
}

.options-container {
  position: absolute;
  top: 160%;
  left: 50%;
  transform: translate(-50%, 0%);
  width: 480px;
  height: 250px;
  border-radius: 10px;
  border: 1px solid white;
  background-color: #232629;
  color: white;
  box-sizing: border-box;
  padding: 20px 20px;
  text-align: center;
  z-index: 2;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media (max-width: 768px) {
  .options-container {
    width: 350px;
  }
}

.option {
  width: calc(33.33% - 10px);
  margin-bottom: 30px; 
}

.option:hover {
  color: rgb(88, 144, 200);
  transition: all .2s;
}
