header {
  background-color: #232629;
  border-bottom: 1px solid white;
  padding: 20px;
}
  
img {
  height: 40px;
  -webkit-touch-callout: none;
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
}
  
@media (max-width: 768px) {
  img {
    height: 30px;
  }
}