@font-face {
  font-family: "NSacEB";
  src: url("/public/NanumSquare_acEB.ttf") format("truetype");
  font-weight: normal;
}

* {
  font-family: 'NSacEB', sans-serif;
}

body {
  margin: 0;
  background-attachment: fixed;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 768px) {
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
}

.App {
  text-align: center;
  background-color: #232629;
  height: 100vh;
}

@media (max-width: 768px) {
  .App {
    height: 120vh;
  }
}

.separator {
  display: flex;
  align-items: center;
  text-align: center;
  max-width: 50%;
  margin: 0 auto;
}

.separator::before,
.separator::after {
  content: "";
  flex: 1;
  border-bottom: 1px solid #ccc;
}

.separator::before {
  margin-right: 0.5em;
}

.separator::after {
  margin-left: 0.5em;
}

.translate-btn {
  width: 20%;
  padding: 15px;
  border: 1px solid #ccc;
  border-color: #ccc;
  border-radius: 10px;
  background-color: #232629;
  font-size: 16px;
  line-height: 1.5;
  color: white;
  align-items: center;
  margin: 0 auto;
  position: relative;
  display: block;
  transition: all 0.5s ease;
}

.translate-btn:hover {
  border-radius: 30px;
  border-color: #59C664;
  color: #59C664;
}

@media (max-width: 768px) {
  .translate-btn {
    width: 50%;
  }
  .separator {
    max-width: 70%;
  }
}